.library {
    position: fixed;
    top: 0;
    left: 0;
    height: 103%;
    width: 20rem;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    background: white;
    box-shadow: 2px 2px 50px rgb(204, 204, 204);
    overflow: scroll;
    padding-bottom: 20px;

    h2 {
        padding: 2rem;
    }
}

.library-song {
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
    transition: background 0.3s ease;;
    img {
        width: 30%;
    }
    &:hover {
        background: rgba(125, 252, 246, 0.3);
    }
}

.song-description {
    padding-left: 1rem;
    h3 {
        font-size: 1rem;
    }
    h4 {
        font-size: 0.75rem;
    }
}

* {
    scrollbar-width: thin;
    scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
    &::-webkit-scrollbar {
        width: 2px;
    }
    &::-webkit-scrollbar-track {
        background: transparent;
    }
    &::-webkit-scrollbar-thumb {
        background-color: rgba(155, 155, 155, 0.5);
        border-radius: 20px;
        border: transparent;
    }
}

.selected {
    background: rgba(127, 125, 252, 0.3);
    &:hover {
        background: rgba(127, 125, 252, 0.3);
    }
}

.active-library {
    transform: translateX(0%);
    opacity: 1;
}

@media screen and (max-width: 768px) {
    .library {
        width: 100%;
    }
}