* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    svg {
        color: rgb(43, 43, 43);
    }
}
body{
    transition: all 0.3s;
    user-select: none;
    font-family: 'Lato', sans-serif;
}

h1,h2 {
    color: rgb(54, 54, 54);
}

h3, h4 {
    font-weight: 400;
    color: rgb(100, 100, 100);
}

.App {
    transition: all 0.5s ease;
}
.library-active{
    margin-left: 30%;
}

@import './player';
@import './song';
@import './library';
@import './nav';