nav {
    min-height: 10vh;
    align-items: center;
    display: flex;
    justify-content: space-around;
    margin-top: 13px;
    margin-bottom: 17px;

    h1,
    svg {
        color: rgb(65, 65, 65);
        user-select: none;
        transition: all 0.3s ease;
        cursor: pointer;
    }

    button {
        color: rgb(65, 65, 65);
        background: transparent;
        border: 2px solid rgb(65, 65, 65);
        cursor: pointer;
        padding: 0.5rem;
        transition: margin 0.5s ease,width 0.7s ease, all 0.3s ease;

        &:hover {
            background: rgb(65, 65, 65);
            color: white;
            svg {
                color: white;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    nav button {
            z-index: 10;
    }
    .library-active{
        nav button {
            margin-top: 1.3%;
        }
    }
}
